<!-- Listado de opiniones, usados por las vistas de "Opiniones" y "Administrador.vue" -->
<template>
    <div class="container-lg">
        <div v-if="obtenerListaOpiniones.length > 0">
            <!-- Para observar opiniones, usado por "Opiniones.vue"  -->
            <div v-if="tipo_lista == 'tipo_observador'">
                <div class="accordion" id="accordionExample">
                    <div v-for="(item, clave) in obtenerListaOpiniones" v-bind:key="clave">
                        <div class="accordion-item">
                            <h2 class="accordion-header" v-bind:id="accordionId(clave, 'heading')">
                                <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" v-bind:data-bs-target="accordionId(clave, '#collapse')" aria-expanded="true" v-bind:aria-controls="accordionId(clave, 'collapse')">Opinion creada por {{ item.autor }} para el juego {{ item.juego }}</button>
                            </h2>
                            <div v-bind:id="accordionId(clave, 'collapse')" class="accordion-collapse collapse" v-bind:aria-labelledby="accordionId(clave, 'heading')" data-bs-parent="#accordionExample">
                                <div class="accordion-body">
                                    <strong>Opinion: </strong>{{ item.opinion }}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- Para editar y/o eliminar las opiniones, usado por "Administrador.vue" -->
            <div v-else-if="tipo_lista == 'tipo_editor'" class="container-lg d-flex">
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col" style="width: 5%;">#</th> 
                            <th scope="col" style="width: 10%;">Persona</th>
                            <th scope="col" style="width: 25%;">Juego</th>
                            <th scope="col" style="width: 50%;">Opinion</th>
                            <th scope="col" style="width: 5%;"></th>
                            <th scope="col" style="width: 5%;"></th>
                        </tr>
                    </thead>
                    <tbody v-for="(item, clave) in obtenerListaOpiniones" v-bind:key="clave">
                        <tr>
                            <th scope="row">{{ clave }}</th>
                            <td id="row_autor">{{ item.autor }}</td>
                            <td id="row_juego">{{ item.juego }}</td>
                            <td id="row_opinion">{{ item.opinion }}</td>
                            <td><button type="button" class="btn btn-danger" v-on:click="accionarBorrarOpinion(clave)">Eliminar</button></td>
                            <td><router-link v-bind:to="{ name: 'editor', params: { opinionid: clave } }"><button type="button" class="btn btn-primary">Editar</button></router-link></td>
                       </tr>
                    </tbody>
                </table>
            </div>        
        </div>
        <div v-else>
            <div class="alert alert-danger" role="alert" style="width: 100%;">{{ msg_lista_vacia }}</div>
        </div>
    </div>
</template> 
<script>
import { mapActions, mapGetters } from 'vuex'

export default 
{
    data()
    {
        return {
            opiniones_titulo: "Opiniones"
        }
    },
    props:
    {
        tipo_lista: String,
        msg_lista_vacia: String
    },
    computed: mapGetters(['obtenerListaOpiniones']),
    methods:
    {
        accordionId: function(id, param_string) 
        {
            return `${param_string}_${id}`
        },
        ...mapActions(['accionarBorrarOpinion'])
    }
}
</script>

<style scoped>
    h1
    {
        margin: 16px;
        text-align: center;
    }
</style>