import Vue from 'vue'
import VueRouter from 'vue-router'
import Portada from '../views/Portada'
import Opiniones from '../views/Opiniones'
import Administrador from '../views/Administrador'
import Editor from '../views/Editor'
import NotFound from '../views/NotFound'

Vue.use(VueRouter)

const routes = 
[

    {
        path: '/',
        component: Portada
    },
    {
        path: '/opiniones',
        component: Opiniones
    },
    {
        path: '/administrador',
        component: Administrador
    },
    {
        path: '/administrador/editor/:opinionid',
        name: 'editor',
        component: Editor,
        props: true
    },
    {
        path: '*',
        component: NotFound
    },
]

const router = new VueRouter(
{
    mode: 'history',
    base: process.env.NODE_ENV === "production" ? '/Franco_GamesOpinionSite/' : '/',
    routes
})

export default router
