<!-- Vista para editar la opinion seleccionada -->
<template>
    <div>
        <h1>Editando la opinion de: {{ usuario_juego }}</h1>
        <div class="container-lg">
            <div class="mb-3">
                <label for="txt1" class="form-label">Nombre:</label>
                <textarea class="form-control" id="txt1" rows="1" v-model="usuario_nombre"></textarea>
            </div>
            <div class="mb-3">
                <label for="txt2" class="form-label">Opinion:</label>
                <textarea class="form-control" id="txt2" rows="3" v-model="usuario_opinion"></textarea>
            </div>
            <router-link to="/administrador"><button type="button" class="btn btn-primary">Regresar</button></router-link>
            <button type="button" class="btn btn-success" id="liveToastBtn" v-on:click="accionarModificarOpinion( [ usuario_id, usuario_nombre, usuario_opinion ] )">Guardar</button>
        </div>
    </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'

export default 
{
    data()
    {
        return {
            usuario_id: "",
            usuario_juego: "",
            usuario_nombre: "",
            usuario_opinion: ""
        }
    }, 
    created()
    {
        this.setUsuario()
    },
    props: ['opinionid'],
    methods: 
    {
        setUsuario: function()
        {
            this.usuario_id = this.opinionid
            this.usuario_nombre = this.obtenerListaOpiniones[this.opinionid].autor
            this.usuario_juego = this.obtenerListaOpiniones[this.opinionid].juego
            this.usuario_opinion = this.obtenerListaOpiniones[this.opinionid].opinion
        },
        ...mapActions(['accionarModificarOpinion']),
    },
    computed:
    {
        ...mapGetters(['obtenerListaOpiniones'])
    }
}
</script>
<style scoped>
    button
    {
        margin-right: 16px;
    }
</style> 

<style scoped>
    h1
    {
        margin: 16px;
        text-align: center;
    }
</style>