import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store(
{
    state: 
    {
        almacen_api: "",
        almacen_api_lista: false,
        lista_opiniones: 
        [ 
            { autor: "Juanito", juego: "Grand Theft Auto V", opinion: "Esta bueno, aunque hay mucho cheater en el online..." }, 
            { autor: "Elias", juego: "Left 4 Dead 2", opinion: "Excelente para jugar con tus amigos" }, 
            { autor: "Maria", juego: "The Elder Scrolls V: Skyrim", opinion: "Muy bueno !" } 
        ]
    },
    getters:
    {
        obtenerListaJuegos: (state) => 
        { 
            return state.almacen_api.results
        },
        obtenerListaOpiniones: (state) => 
        { 
            return state.lista_opiniones
        },
        obtenerApiCargada: (state) => 
        { 
            return state.almacen_api_lista
        }
    },
    mutations: 
    {
        setearDatosAPI: (state, param_api) =>
        {
            state.almacen_api = param_api
        },
        setearAPILista: (state, param_status) =>
        {
            state.almacen_api_lista = param_status
        },
        setearBorrarOpinion: (state, param_id) =>
        {
            state.lista_opiniones.splice(param_id, 1)
        },
        setearModificarOpinion: (state, param_array) => // [ usuario_id, usuario_nombre, usuario_opinion ]
        {
            state.lista_opiniones[param_array[0]].autor = param_array[1]
            state.lista_opiniones[param_array[0]].opinion = param_array[2]
            alert("Cambios guardados")
        },
        setearAñadirOpinion: (state, param_array) =>  // [ modal_nombre_usuario, modal_titulo, modal_opinion_usuario ]
        {
            state.lista_opiniones.push( { autor: param_array[0], juego: param_array[1], opinion: param_array[2] } )
            console.log("%cAccionado el mutations !", "color: #00FF00")
            console.log(param_array)
        }
    },
    actions: 
    {
        accionarDatosAPI({commit}, param_url)
        {
            fetch(param_url)
            .then(response => response.json())
            .then(json => 
            {
                commit('setearDatosAPI', json)
                commit('setearAPILista', true)
            })
        },
        accionarBorrarOpinion({commit}, param_id)
        {
            commit('setearBorrarOpinion', param_id)
        },
        accionarModificarOpinion({commit}, param_array) // [ usuario_id, usuario_nombre, usuario_opinion ]
        {
            commit('setearModificarOpinion', param_array)
        },
        accionarAñadirOpinion({commit}, param_array) // [ modal_nombre_usuario, modal_titulo, modal_opinion_usuario ]
        {
            console.log("%cAccionado el actions !", "color: #00FF00")
            console.log(param_array)

            commit('setearAñadirOpinion', param_array)
        }
    },
    modules: 
    {

    }
})
