<!-- Vista del administrador -->
<template>
    <div>
        <h1 v-text="administracion_titulo"></h1>
        <ListadoOpiniones v-bind:tipo_lista="tipo_lista" v-bind:msg_lista_vacia="msg" ></ListadoOpiniones>
    </div>    
</template>

<script>
import ListadoOpiniones from '../components/ListadoOpiniones'
import { mapActions, mapGetters } from 'vuex'

export default 
{
    data()
    {
        return {
            administracion_titulo: "Administracion de la lista de opiniones",
            tipo_lista: "tipo_editor",
            msg: "No existen opiniones por administrar"
        }
    },
    components:
    {
        ListadoOpiniones
    },
    computed: 
    { 
        ...mapGetters(['obtenerListaOpiniones']),
    },
    methods:
    {
        ...mapActions(['accionarBorrarOpinion'])
    }
}
</script>

<style scoped>
    h1
    {
        margin: 16px;
        text-align: center;
    }
</style>