<!-- Vista principal de la pagina -->
<template>
    <div>
        <h1 v-text="portada_titulo"></h1>
        <div class="container-md">
            <div v-if="obtenerApiCargada == true">
                <div class="row row-cols-1 row-cols-md-3 g-4">
                    <div v-for="(item, clave) in obtenerListaJuegos" v-bind:key="clave">
                        <div class="col">
                            <div class="card h-100">
                                <img v-bind:src="item.background_image" class="card-img-top" alt="card-alt1" width="192px" height="192px">
                                <div class="card-body">
                                    <ul class="list-group list-group-flush">
                                        <li class="list-group-item"><h4 class="card-title" id="li_game_name">{{ item.name }}</h4></li>
                                        <li class="list-group-item">Fecha de lanzamiento: {{ item.released }}</li>
                                        <li class="list-group-item">Valoracion: {{ item.rating }}</li>
                                        <li class="list-group-item">Ultima actualizacion: {{ item.updated }}</li>
                                        <li class="list-group-item">
                                            <div class="d-flex justify-content-center">
                                                <button type="button" class="btn btn-warning" data-bs-toggle="modal" data-bs-target="#modal_opinion" v-on:click="modalParametros(clave)">Opinar</button>
                                            </div>
                                        </li>
                                    </ul>    
                                </div>
                            </div> 
                        </div>
                    </div>
                </div>
            </div>
            <div v-else class="loadingBox">
                <div class="spinner-border text-success" style="width: 5rem; height: 5rem;" role="status">
                </div>
                <h2>Cargando</h2>
            </div>
        </div>
        <div class="modal fade" id="modal_opinion" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Escribe tu opinion para el juego {{ modal_titulo }}</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div class="mb-3">
                            <label for="txt1" class="form-label">Nombre:</label>
                            <textarea name="usuario" class="form-control" id="txt1" rows="1" placeholder="Escribe tu nombre..." v-model="modal_nombre_usuario"></textarea>
                        </div>
                        <div class="mb-3">
                            <label for="txt2" class="form-label">Opinion:</label>
                            <textarea name="opinion" class="form-control" id="txt2" rows="3" placeholder="Escribe tu opinion..." v-model="modal_opinion_usuario"></textarea>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <h5>{{ modal_estado }}</h5>
                        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cerrar</button>
                        <button class="btn btn-success" v-on:click="probarOpinion">Añadir opinion</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default 
{
    data()
    {
        return {
            portada_titulo: "Lista de juegos disponibles",
            modal_titulo: "",
            modal_nombre_usuario: "",
            modal_opinion_usuario: "",
            modal_estado: ""
        }
    },
    computed: 
    {
        ...mapGetters(['obtenerListaJuegos', 'obtenerApiCargada'])
    },   
    methods:
    {
        modalParametros: function(param_juego)
        {
            this.modal_estado = ""
            this.modal_titulo = `${this.obtenerListaJuegos[param_juego].name}`
        },
        probarOpinion: function()
        {
            if (!this.modal_nombre_usuario.length)
            {
                this.modal_estado = "Debe añadir su nombre"
                return
            }
            else if (!this.modal_opinion_usuario.length)
            {
                this.modal_estado = "Debe añadir su opinion"
                return
            }

            this.modal_estado = "Opinion añadida !"
            this.accionarAñadirOpinion( [ this.modal_nombre_usuario, this.modal_titulo, this.modal_opinion_usuario ] )
        },
        ...mapActions(['accionarAñadirOpinion'])
    }
    
}
</script>
<style scoped>
    h1
    {
        margin: 16px;
        text-align: center;
    }
    img
    {
        object-fit: cover;
    }
    .loadingBox
    {
        display: flex;
        flex-direction: column;
        
        align-items: center;
    }
    .container-md
    {
        margin-bottom: 32px;
    }
</style>
 